import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import Link from '@material-ui/core/Link';
import { Link as RouterLink } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import GradeIcon from '@material-ui/icons/Grade';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import Style from './style';

interface HomeTile {
	icon: any;
	head: string;
	body: string;
	link: string;
	linkText: string;
	externalLink?: boolean;
}

interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	items: Array<HomeTile>;
}
/**
 * HomepageTile for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class HomepageTile extends React.PureComponent<Props> {
	componentDidMount() {}

	getIcon = (name) => {
		const { classes } = this.props;
		switch (name) {
			case 'property':
				return <VpnKeyIcon className={classes.icon} />;
			case 'supply':
				return <LocalShippingIcon className={classes.icon} />;
			case 'job':
				return <GradeIcon className={classes.icon} />;
			case 'social':
				return <ArrowForwardIcon className={classes.icon} />;
			default:
				return <img src="/images/icon-menu.png" alt="menu-icon" className={classes.icon} />;
		}
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, items } = this.props;
		return (
			<Paper square className={classes.paper}>
				<div className={classes.root}>
					{items.map((item, index) => (
						<>
							<div className={classes.tile} key={item.head}>
								<div className={classes.head}>
									<span className={classes.iconContainer}>
										{this.getIcon(item.icon)}
									</span>
									<span>{item.head}</span>
								</div>
								<div className={classes.body}>{item.body}</div>
								<div className={classes.foot}>
									<Link
										key={item.head}
										className={classes.link}
										to={item.link}
										href={item.externalLink ? item.link : ''}
										target={item.externalLink ? '__blank__' : ''}
										color="inherit"
										component={item.externalLink ? 'a' : (RouterLink as any)}
									>
										{' '}
										<span>{item.linkText}</span>
										<ArrowForwardIcon className={classes.arrow} />
									</Link>
								</div>
							</div>
							{index < items.length - 1 && (
								<Divider
									orientation="vertical"
									flexItem
									className={classes.divider}
								/>
							)}
						</>
					))}
				</div>
			</Paper>
		);
	}
}
export default withStyles(Style, { name: 'DmartCorpFreshHomepageTile' })(HomepageTile);
